import React, { Component } from 'react';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider';

export default class NonlabviewtableCell extends Component {
    state ={
        Result:[]
    }
    componentWillReceiveProps() {
        console.log("###componentWillReceiveProps");
    }
    componentDidMount() {
        //call API
        let { caseId, hisId } = this.props;
        const { month, seq } = this.props;
        if(caseId !== undefined && hisId !== undefined && month !== undefined && seq !== undefined) {
            hisId = hisId.trim();
            caseId = caseId.trim();
            ApiProvider.getStoredProcedureRESDISP(hisId, month, caseId, seq)
            .then(result => {
                console.log("getStoredProcedureRESDISP OK " + JSON.stringify(result.data));

            })
            .catch(err => {
                console.log("getStoredProcedureRESDISP NG " + JSON.stringify(err));
            });
        }
    }
    render() {
        function resultFormat(resultdata){
            if (Array.isArray(resultdata)) {
                if (resultdata.length === 0) {
                    return "--"
                }
                else{
                    //Call Parse
                    return "Parse"
                }
            }
            return "Na"
        }
        const { time, name, status } = this.props;
        const ResultData = resultFormat(this.state.Result)
        
        return(
            <div style={{ height: "50px", display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr" }}>
                <t3 className='nonlabteblecell'>{moment(time,'YYYY-MM-DD').format('MM/DD')}</t3>
                <t3 className='nonlabteblecell'>{name}</t3>
                <t3 className='nonlabteblecell'>{status ? "已發報告" : "已開單"}</t3>
                <t3 className='nonlabteblecell'>{ResultData}</t3>
            </div>
        )
    }
}
