import React, { Component } from 'react';
import * as d3 from "d3";
import './Wardcardvitalsign.scss';
import { mmddformat } from '../Function/Timeformat'
import * as Apiprovider from '../API/ApiProvider'
import moment from 'moment';
// { center_monitor } from '../Centormonitor_data'



class WardcardvitalsignChart extends Component {
    state = {
        CenterMonitorData: []
    }
    componentDidUpdate() {
        const { svg_key } = this.props
        d3.select("#" + svg_key).remove()
        this.drawChart()

    }
    componentDidMount() {
        const { HISID, type } = this.props
        console.log(type)
        const HID = HISID.toString().trim()
        const et = moment(moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf() + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss")
        const st = moment(moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf() + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss")
        console.log(HISID, et, st, type)
        Apiprovider.getPatientWardValue(et, HID, st, type)
            .then(data => {
                console.log(data.data)
                const source = data.data
                if (Array.isArray(source)) {
                    if (source.length != 0) {
                        this.setState({
                            CenterMonitorData: data.data
                        })
                    }
                }

            })
            .catch(err => {
                console.log(err)
            })
    }
    cal_svg_hight() {
        const { axisTop, axisBot } = this.props
        if (axisTop) {
            if (axisBot) {
                return 160
            }
            else {
                return 130
            }
        }
        else {
            if (axisBot) {
                return 100
            }
            else {
                return 90
            }
        }
    }

    cal_svg_transform() {
        const { axisTop, axisBot } = this.props
        if (axisTop) {
            if (axisBot) {
                return 70
            }
            else {
                return 50
            }
        }
        else {
            if (axisBot) {
                return 5
            }
            else {
                return 5
            }
        }
    }

    drawChart() {
        function multiFormat(date) {
            if (formatHour(date) % 4 === 0) {
                return formatHour(date)
            }
            else return null
        }
        function multibotformat(date) {
            if (new Date(date).getHours() === 0) {
                return mmddformat(date)
            }
            else return null
        }

        const { svg_key, circlrcolor, data, axisTop, id_key, axisBot } = this.props
        const width = window.screen.availWidth * 0.25, height = 70, transformmargin = this.cal_svg_transform(), max = 200, min = 0, margin = 20
        const borderwidth = width / 27
        var time_now = new Date().getTime();

        var time_array = []
        var time_axis_array = []

        for (let index = 0; index < 25; index++) {
            time_array.push({ key: "time", x_axis_time: time_now - index * 60 * 60 * 1000 })
            time_axis_array.push(time_now - index * 60 * 60 * 1000)
        }
        var time_array_reverse = time_array.reverse()
        var time_axis_array_reverse = time_axis_array.reverse()
        var formatHour = d3.timeFormat("%H")

        var x = d3.scaleLinear()
            .domain([time_array_reverse[0].x_axis_time, time_array_reverse[24].x_axis_time])
            .range([0, width - 40]);


        //x和y的比例尺
        var y = d3.scaleLinear()
            .domain([min, max])
            .range([height, 0])



        var yAxis = d3.axisLeft(y)
            .tickValues([min, max])
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(10);

        var xAxisBottom = d3.axisBottom(x)
            .tickValues(time_axis_array_reverse)
            .tickSize(0, 0)
            .tickPadding(height + 10)
            .tickFormat(function (d) { return multibotformat(d); });


        var xAxis = d3.axisTop(x)
            .tickValues(time_axis_array_reverse)
            .tickSize(0, 0)
            .tickPadding(margin)
            .tickFormat(function (d) { return multiFormat(d); });

        var svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_key)
            .attr("viewBox", [0, 0, width, this.cal_svg_hight()])
            .append("g")
            .attr("transform", "translate(" + 30 + "," + transformmargin + ")");


        //y軸
        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");


        if (axisTop) {
            svg.append("g")
                .call(xAxis)
                .attr("class", "axis");
        }

        if (axisBot) {
            svg.append("g")
                .call(xAxisBottom)
                .attr("class", "axis");
        }

        //上下虛線
        svg.append('line')
            .attr('x1', 0)
            .attr('y1', y(max))
            .attr('x2', width)
            .attr('y2', y(max))
            .style('stroke', '#E0E0E0')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);

        svg.append('line')
            .attr('x1', 0)
            .attr('y1', y(min))
            .attr('x2', width)
            .attr('y2', y(min))
            .style('stroke', '#E0E0E0')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);


        for (let index = 0; index < time_array_reverse.length; index++) {
            svg.append('line')
                .attr('x1', x(time_array_reverse[index].x_axis_time))
                .attr('y1', 0)
                .attr('x2', x(time_array_reverse[index].x_axis_time))
                .attr('y2', height)
                .style('stroke', '#E0E0E0')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);
        }




        var sim_data = this.state.CenterMonitorData
        const st = time_array_reverse[0].x_axis_time
        var ListTitle = []
        for (let index = 0; index < 24; index++) {
            var List = []
            const startTime = st + ((index) * 60 * 60 * 1000)
            const endTime = st + ((index + 1) * 60 * 60 * 1000)
            var List = []
            for (let index = 0; index < sim_data.length; index++) {
                const element = sim_data[index];
                if (startTime <= element.time && element.time < endTime) {
                    List.push(element)
                }
            }
            ListTitle.push(List)
        }
        console.log(sim_data, List)


        for (let index = 0; index < ListTitle.length; index++) {
            const element = ListTitle[index];
            svg.append('line')
                .attr('x1', x(element.time))
                .attr('y1', y(20))
                .attr('x2', x(element.time))
                .attr('y2', y(120))
                .style('stroke', circlrcolor)
                .style('opacity', 0.5)
                // .style('stroke', 'red')
                .style('stroke-width', borderwidth)


        }
        for (let index = 0; index < sim_data.length; index++) {


            const element = sim_data[index];
            if (Array.isArray(element)) {
                console.log(eledata)
                const data_sorted = eledata.sort(d3.ascending)
                const q1 = d3.quantile(data_sorted, .25)
                const q3 = d3.quantile(data_sorted, .75)
                const interQuantileRange = q3 - q1
                const minbox = q1 - 1.5 * interQuantileRange
                const maxbox = q1 + 1.5 * interQuantileRange




                svg.append("line")
                    .attr("x1", x(element.time))
                    .attr("x2", x(element.time))
                    .attr("y1", y(minbox))
                    .attr("y2", y(maxbox))
                    .attr("stroke", circlrcolor)
                    .style('stroke-width', 1)

                svg.append("rect")
                    .attr("x", x(element.time) - boxwidth / 2)
                    .attr("y", y(q3))
                    .attr("height", (y(q1) - y(q3)))
                    .attr("width", boxwidth)
                    .attr("stroke", circlrcolor)
                    .style("fill", "rgba(255,255,255,1)")
                    .style('stroke-width', 1);

                // svg
                //     .selectAll("toto")
                //     .data([minbox, median, maxbox])
                //     .enter()
                //     .append("line")
                //     .attr("x1", x(element.time) - boxwidth / 2)
                //     .attr("x2", x(element.time) + boxwidth / 2)
                //     .attr("y1", function (d) { return (y(d)) })
                //     .attr("y2", function (d) { return (y(d)) })
                //     .attr("stroke", circlrcolor)
                //     .style('stroke-width', 1);
                svg
                    .selectAll("toto")
                    .data([minbox, maxbox])
                    .enter()
                    .append("line")
                    .attr("x1", x(element.time) - boxwidth / 2)
                    .attr("x2", x(element.time) + boxwidth / 2)
                    .attr("y1", function (d) { return (y(d)) })
                    .attr("y2", function (d) { return (y(d)) })
                    .attr("stroke", circlrcolor)
                    .style('stroke-width', 1);
            }
            const eledata = element.Data
            const boxwidth = 10


        }

    }

    render() {
        const { id_key } = this.props
        const source = this.state.CenterMonitorData
        console.log(source)
        if (source.length === 0) {
            return null
        }
        return (
            <div id={id_key}></div>
        );
    }
}

export default WardcardvitalsignChart;